// @import "./../tools.scss";

@import "./../../scss/tools.scss";

.header {
    grid-area: h;
    background-color: #fff;
    color: #181818;
    box-shadow: 0px 10px 40px rgba(89, 120, 150, 0.06);
    margin-bottom: 40px;

    padding: 17px 40px;
    display: flex;
    flex-wrap: wrap;

    align-items: center;

    &-title {
        font-size: 18px;
        line-height: 20px;
        font-weight: 400;
    }
    &-btn-block {
        padding: 0px 20px;
    }
    .header-logo img {
        width: auto;
        height: 30px;
        padding: 10px;
    }


    .header-menu {
        display: flex;
        flex-wrap: nowrap;

        .item {
            padding: 0px 10px;

            font-family: Gotham Pro;
            font-size: 12px;
            line-height: 11px;
            text-align: center;
            text-transform: uppercase;

            color: #181818;
        }
    }
    .header-profile {
        padding: 0px 30px;
    }
    .header-login-block {
        padding: 0px 30px;
        a {
            color: #222;
            text-decoration: none;
            &:hover, &:focus {
                color: #999;
            }
        }
    }
}

